#services .front-end {
  display: flex;
  flex-direction: column;
  align-self: end;
}
#services .box {
  background-color: #0000007a !important;
  color: #ffffff;
  border-radius: 1rem;
}
#services .front-end h2 {
  width: fit-content;
  color: #0dcca5;
}
#services .front-end h2::before {
  content: "";
  position: absolute;
  width: 17rem;
  height: 0rem;
  border-style: dashed;
  left: 100%;
  top: 50%;
  transform: translateY(-50%);
  color: white;
}

#services .back-end {
  display: flex;
  flex-direction: column;
  align-self: end;
}
#services .back-end .box {
  background-color: #0000007a !important;
  color: #ffffff;
  border-radius: 1rem;
}
#services .back-end h2 {
  width: fit-content;
  color: #FBB040;
  align-self: end;
}
#services .back-end h2::before {
  content: "";
  position: absolute;
  width: 17rem;
  border-style: dashed;
  right: 100%;
  top: 50%;
  transform: translateY(-50%);
  color: white;
}

#services .dev-ops h2 {
  color: #C0CB21;
}
#services .dev-ops h2::before {
  content: "";
  position: absolute;
  left: 50%;
  bottom: calc(100% + 0.25rem);
  height: 2.5rem;
  border-style: dashed;
  transform: translateX(-50%);
  color: white;
}

#services .content  a{
  display: block;
  width: fit-content;
  margin: 0 auto 10px;
  font-size: 1rem;
  font-weight: bold;
  padding: 0.5rem 1.25rem;
  border-radius: 4px;
  background-color: #C0CB21;
}









